import React from 'react';
import PropTypes from 'prop-types';

const getColorCode = (color) => {
  switch (color) {
    case '':
      return 'white';
    case '':
      return 'black';
    case '':
      return 'orange';
    case '':
      return 'blue';
    case '':
      return 'green';
    case '':
      return 'pink';
    case '':
      return 'gray';
    case '':
      return 'light-blue';
    case '':
      return 'beige';
    case '':
      return 'red';
    case '':
      return 'burgundy';
    case '':
      return 'brown';
    default:
      return null;
  }
};

const Choose = ({ thing, changeSize, changeColor }) => (
  <div className="choose">
    <div className="sizes">
      <p><strong> : </strong></p>
      <ul className="choose-list">
        {
              thing.availableSizes.map(s => (
                <li key={s}>
                  <button className="size-button" onClick={() => changeSize(s)}>{s}</button>
                </li>
              ))
          }
      </ul>
    </div>
    <div className="colors">
      <p><strong> : </strong></p>
      <ul className="choose-list">
        {
                thing.availableColors.map(c => (
                  <li key={c}>
                    <button>
                      <div
                        className="color-button"
                        style={{ backgroundColor: `${getColorCode(c)}` }}
                        onClick={() => changeColor(c.color, c.vendorcode)}
                      />
                    </button>
                  </li>
                ))
            }
      </ul>
    </div>
  </div>
);

Choose.propTypes = {
  changeSize: PropTypes.func,
  changeColor: PropTypes.func,
}

Choose.defaultProps = {
  changeSize: () => {},
  changeColor: () => {},
}

export default Choose;
